.mainvisual {
    position: relative;
    padding: 0;
    .background {
        position: relative;
        width: 100%;
        z-index: 0;
        img {
            position: relative;
            display: block;
            width: 100%;
            height: 100vh;
            min-height: 60.0rem;
        }
    }
    .back-slider {
        position: relative;
        width: 100%;
        height: 100vh;
        min-height: 60.0rem;
        z-index: 0;
        @include media('sp') {
            height: calc(100vh - 6.0rem);
            height: calc( ( var(--vh, 1vh) * 100 ) - 6.0rem );
        }
        img {
            position: relative;
            display: block;
            width: 100%;
            height: 100%;
        }
        @include media('sp'){
            .vegas-slide, .vegas-slide-inner {
                background-position: bottom center !important;
            }
        }
    }
    .cover-title {
        position: absolute;
        left: clamp(1.5rem, calc(50% - 64.0rem), 50%);
        bottom: 6.0rem;
        z-index: 3;
        @include media('sp') {
            left: 2.0rem;
            right: 2.0rem;
            bottom: 4.0rem;
        }
        h2 {
            position: relative;
            font-weight: 500;
            font-size: 3.2rem;
            text-align: left;
            letter-spacing: 0.08em;
            color: #FFFFFF;
            text-shadow: 0 0.4rem 0.4rem rgba($color: #000000, $alpha: 0.4);
            margin-bottom: 4.0rem;
            @include media('sp') {
                font-size: 2.4rem;
                line-height: 1.6;
                margin-bottom: 2.0rem;
            }
        }
        p {
            position: relative;
            font-weight: 500;
            font-size: 1.5rem;
            line-height: 2.0;
            letter-spacing: 0.08em;
            color: #FFFFFF;
            text-shadow: 0 0.4rem 0.4rem rgba($color: #000000, $alpha: 0.4);
            @include media('sp') {
                font-size: 1.3rem;
                line-height: 1.8;
                letter-spacing: 0.07em;
            }
        }
    }
    .cover-action {
        position: absolute;
        right: clamp(1.5rem, calc(50% - 64.0rem), 50%);
        bottom: 6.0rem;
        display: flex;
        z-index: 3;
        @include media('sp') {
            left: 2.0rem;
            right: 2.0rem;
            bottom: 4.0rem;
            display: none;
        }
        & > li {
            position: relative;
            @include media('pc') {
                flex: 0 0 auto;
                width: auto;
            }
            @include media('sp') {
                width: 100%;
                .btn {
                    margin-left: auto;
                    margin-right: auto;
                }
            }
            &:not(:last-child) {
                @include media('pc') {
                    margin-right: 2.0rem;
                }
                @include media('sp') {
                    margin-bottom: 2.0rem;
                }
            }
        }
    }
}

.pageindex {
    position: relative;
    padding: 0;
    .background {
        position: relative;
        width: 100%;
        img {
            display: block;
            width: 100%;
            height: 42.0rem;
        }
    }
}

.pageintro {
    position: relative;
    padding: 8.0rem 0;
    .title {
        position: relative;
        font-weight: 500;
        font-size: 3.2rem;
        text-align: center;
        letter-spacing: 0.08em;
        color: #3E3A39;
        margin-bottom: 2.0rem;
        @include media('sp') {
            font-size: 2.4rem;
        }
    }
    .desc {
        position: relative;
        font-weight: 500;
        font-size: 1.5rem;
        line-height: 1.8;
        text-align: center;
        letter-spacing: 0.08em;
        color: #3E3A39;
    }
}

.breadcrumbs {
    position: relative;
    padding: 1.6rem 0;
    ol {
        display: block;
        list-style: none;
        margin: 0;
        font-family: $font_noto;
        font-size: 1.6rem;
        line-height: 2.4rem;
        font-weight: normal;
        color: #000000;
        @include media('sp') {
            font-size: 1.4rem;
            line-height: 2.4rem;
        }
        a {
            color: $green_color;
            text-decoration: underline;
        }
        li {
            position: relative;
            display: inline;
            &+li {
                &::before {
                    content: "\f105";
                    display: inline-block;
                    font-family: "Font Awesome 6 Free";
                    font-size: inherit;
                    font-weight: 900;
                    font-style: normal;
                    padding-left: 1.2rem;
                    padding-right: 1.2rem;
                    @include media('sp') {
                        padding-left: 1.0rem;
                        padding-right: 1.0rem;
                    }
                }
            }
        }
    }
}

.inner-toppage {
    position: relative;
    padding: 5.0rem 0 6.0rem;
    @include media('sp') {
        padding: 4.0rem 0 5.0rem;
    }
}

.top-banner {
    position: relative;
    padding: 8.0rem 0;
    @include media('sp') {
        padding: 4.0rem 0;
    }
    .content-in {
        position: relative;
        max-width: 80.0rem;
        @include media('sp') {
            max-width: 33.5rem;
        }
    }
    .banner {
        position: relative;
        width: 100%;
        @include media('sp') {
            img {
                width: 100%;
            }
        }
    }
    .banner-link {
        position: relative;
        display: block;
        width: 100%;
        cursor: pointer;
        text-decoration: none;
    }
}

.top-concept {
    position: relative;
    padding: 21.0rem 0 21.0rem;
    @include media('pc') {
        min-height: 88.0rem;
    }
    @include media('sp') {
        padding: 6.0rem 0 68.0rem;
    }
    .concept-photo1 {
        position: absolute;
        top: 16.0rem;
        left: 0;
        width: 32.0rem;
        max-width: 100%;
        z-index: 1;
        @include media('sp') {
            top: auto;
            left: auto;
            right: 0;
            bottom: 36.0rem;
            width: 22.4rem;
            height: 28.0rem;
        }
    }
    .concept-photo2 {
        position: absolute;
        top: 44.0rem;
        left: 0;
        width: 32.0rem;
        max-width: 100%;
        z-index: 1;
        @include media('sp') {
            top: auto;
            left: 0;
            bottom: 28.0rem;
            width: 12.0rem;
            height: 20.0rem;
        }
    }
    .concept-photo3 {
        position: absolute;
        top: 16.0rem;
        right: 0;
        width: 32.0rem;
        max-width: 100%;
        z-index: 1;
        @include media('sp') {
            top: auto;
            left: auto;
            display: none;
            opacity: 0;
            visibility: hidden;
            right: 0;
            bottom: 0;
            width: 20.0rem;
            height: 20.0rem;
        }
    }
    .concept-photo4 {
        position: absolute;
        top: 52.0rem;
        right: 0;
        width: 32.0rem;
        max-width: 100%;
        z-index: 1;
        @include media('sp') {
            top: auto;
            left: auto;
            display: none;
            opacity: 0;
            visibility: hidden;
            right: 0;
            bottom: 0;
            width: 20.0rem;
            height: 20.0rem;
        }
    }
    .concept-photo5 {
        position: absolute;
        bottom: 8.0rem;
        right: 0;
        width: 20.0rem;
        max-width: 100%;
        z-index: 1;
        @include media('pc') {
            display: none;
            visibility: hidden;
            opacity: 0;
        }
    }
    .container {
        position: relative;
        z-index: 3;
    }
    .content-in {
        max-width: 80.0rem;
        @include media('sp') {
            max-width: 100%;
        }
    }
    .concept-panel {
        position: relative;
        max-width: 100%;
        width: fit-content;
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        padding: 4.0rem;
        margin-left: auto;
        margin-right: auto;
        @include media('sp') {
            padding: 0;
            width: 100%;
            min-height: 33.4rem;
        }
        .title {
            position: relative;
            font-weight: 500;
            font-size: 4.0rem;
            line-height: 1.6;
            letter-spacing: 0.08em;
            writing-mode: vertical-rl;
            text-orientation: upright;
            margin-left: 3.2rem;
            color: #3E3A39;
            margin-left: 4.0rem;
            @include media('sp') {
                font-size: 3.2rem;
                line-height: 1.5;
                white-space: nowrap;
                margin-left: 1.0rem;
            }
            // small {
            //     vertical-align: text-top;
            // }
        }
        .desc {
            position: relative;
            font-weight: 500;
            font-size: 1.5rem;
            line-height: 2.0;
            letter-spacing: 0.08em;
            writing-mode: vertical-rl;
            text-orientation: upright;
            color: #3E3A39;
            @include media('sp') {
                font-size: 1.4rem;
                line-height: 1.8;
                white-space: nowrap;
            }
        }
    }
}

.top-plan {
    position: relative;
    padding: 8.0rem 0;
    @include media('sp') {
        padding: 12.0rem 0 8.0rem;
        .content-in {
            max-width: 100%;
        }
    }
    .plan-title {
        position: relative;
        font-weight: 500;
        font-size: 3.2rem;
        letter-spacing: 0.08em;
        color: #3E3A39;
        text-align: center;
        margin-bottom: 4.0rem;
        @include media('sp') {
            font-size: 2.4rem;
            margin-bottom: 4.0rem;
        }
    }
    .plan-content {
        position: relative;
    }
    .plan-item-list {
        position: relative;
        @include media('pc') {
            display: flex;
            justify-content: center;
        }
        .plan-item {
            position: relative;
            @include media('pc') {
                flex: 0 0 auto;
                width: auto;
                padding: 0 2.0rem;
            }
            .thumb {
                position: relative;
                width: 32.0rem;
                @include media('sp') {
                    width: 100%;
                }
            }
        }
    }
}

.top-property {
    position: relative;
    padding: 0 clamp(2.0rem, calc(50% - 64.0rem), 50%) 8.0rem 0;
    @include media('sp') {
        padding: 0 0 8.0rem;
    }
    .property-main {
        position: relative;
        z-index: 0;
        .property-photo {
            position: relative;
            width: 100%;
            @include media('pc') {
                aspect-ratio: 136 / 56;
            }
            img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: fill;
                @include media('sp') {
                    height: 28.2rem;
                    object-fit: cover;
                }
            }
        }
    }
    .property-photos {
        position: relative;
        display: flex;
        width: 100%;
        max-width: clamp(0rem, calc(100% - 58.4rem), 80%);
        z-index: 0;
        @include media('sp') {
            max-width: 100%;
        }
        .property-photo {
            position: relative;
            flex: 0 0 auto;
            width: 50%;
            @include media('pc') {
                aspect-ratio: 388 / 315;
            }
            img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: fill;
                @include media('sp') {
                    height: 14.1rem;
                    object-fit: cover;
                }
            }
        }
    }
    .property-panel {
        position: relative;
        background: #FBFBFB;
        z-index: 3;
        @include media('pc') {
            position: absolute;
            right: clamp(0rem, calc(50% - 66.0rem), 50%);
            bottom: 8.0rem;
            padding: 6.4rem 2.0rem 8.0rem 6.4rem;
            width: 60.4rem;
        }
        @include media('sp') {
            width: 100%;
            padding: 2.0rem 2.0rem;
        }
        .title {
            position: relative;
            font-weight: 500;
            font-size: 3.2rem;
            letter-spacing: 0.08em;
            color: #3E3A39;
            margin-bottom: 2.0rem;
            @include media('sp') {
                font-size: 2.4rem;
                margin-bottom: 1.0rem;
            }
        }
        .desc {
            position: relative;
            font-family: $font_gothic;
            font-weight: 500;
            font-size: 1.5rem;
            line-height: 1.8;
            letter-spacing: 0.08em;
            color: #3E3A39;
        }
        .btn {
            position: relative;
            background: #EDEDED;
            margin-top: 4.0rem;
            margin-left: 0;
            margin-right: auto;
            @include media('pc') {
                min-width: 28.0rem;
            }
            @include media('sp') {
                margin-left: auto;
                margin-right: auto;
            }
        }
    }
    @include media('pc') {
        &.type2 {
            padding: 0 0 8.0rem clamp(2.0rem, calc(50% - 64.0rem), 50%);
            .property-photos {
                position: relative;
                margin-left: auto;
                margin-right: 0;
            }
            .property-panel {
                left: clamp(0rem, calc(50% - 66.0rem), 50%);
                right: auto;
                padding: 6.4rem 6.4rem 8.0rem 2.0rem;
            }
        }
    }
}

