                    }
                }
                .desc {
                    position: relative;
                    font-family: $font_gothic;
                    font-weight: 500;
                    font-size: 1.5rem;
                    line-height: 1.8;
                    letter-spacing: 0.08em;
                    color: #3E3A39;
                    @include media('sp') {
                        margin-bottom: 1.0rem;
                    }
                }
                .btn {
                    position: relative;
                    background: #EDEDED;
                    margin-top: 4.0rem;
                    margin-left: 0;
                    margin-right: auto;
                    @include media('pc') {
                        min-width: 28.0rem;
                    }
                    @include media('sp') {
                        margin-left: auto;
                        margin-right: auto;
                    }
                }
            }
        }
    }
}

.top-news {
