        position: relative;
        font-weight: 500;
        font-size: 3.2rem;
        text-align: left;
        letter-spacing: 0.08em;
        color: #3E3A39;
        @include media('pc') {
            float: left;
            max-width: 20%;
        }
        @include media('sp') {
            font-size: 2.4rem;
            margin-bottom: 2.0rem;
        }
    }
    .content-in {
        max-width: 100.0rem;
    }
    .news-list-wrap {
        position: relative;
        @include media('pc') {
            float: right;
            width: 77.0rem;
            max-width: 80%;
            padding-right: 13.0rem;
        }
    }
    .news-item-list {
        position: relative;
        display: block;
        border-top: 1px solid #DADADA;
        & > li {
            position: relative;
            padding: 2.4rem 0;
            border-bottom: 1px solid #DADADA;
            @include media('sp') {
                padding: 1.6rem 0;
            }
        }
        .news-item {
            position: relative;
            display: block;
            .date {
                position: relative;
                display: block;
                font-family: $font_gothic;
                font-weight: 500;
                font-size: 1.0rem;
