                font-family: $font_gothic;
                font-weight: 500;
                font-size: 1.5rem;
                line-height: 1.8;
                letter-spacing: 0.08em;
                color: #3E3A39;
                @include media('sp') {
                    font-family: $font_mincho;
                }
                a {
                    color: inherit;
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    } 
}

